* {
  box-sizing: border-box;
}
.container {
  width: 50%;
  margin: auto;
}

.form-container {
  display: flex;
  justify-content: space-between;
}

input[type="text"], button[type="submit"] {
  width: 49%;
  padding: 10px;
  margin: 5px 0;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin: auto;
  text-align: center;
}

th, td {
  border: 1px solid black;
  padding: 15px;
}

input[type="checkbox"] {
  width: 20px;
  height: 20px;
  background-color: #eee;
}

button {
  display: block;
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}
button.remove {
  width: 100%;
}
.button-container {
  margin: auto;
}
